import ts, { factory, visitEachChild, getDirectoryPath, normalizeSlashes, SyntaxKind, getLocalNameForExternalImport, collectExternalModuleInfo } from "typescript"
import { generateFactory } from "./createFactoryCode"
import resolve from 'resolve'
import path from "path/posix"
import chokidar from "chokidar"
import { getColumnName } from "../../../Helpers/utils"
import { configModules, ModulesThree } from "./utils"


const { createStringLiteral } = factory
const {
    CREATE_Export_File_Function,
    CREATE_Plus_Token_Nodes,
    CREATE_Const_Variable,
    CREATE_Property_Access_Expression
} = generateFactory



const watcher = chokidar.watch([]);
// let STATEMENTS = []
// let stateNode = CREATE_Call_Function(STATEMENTS)
// let UNICNAME = ts.createUniqueName('AAA')

export const transpilerBefore = {
    // [SyntaxKind.ExportAssignment]: (Node) => {  
    // }, 
    // [SyntaxKind.ArrowFunction]: (NODE, visitor, CTX) => { 
    //     // return visitEachChild(NODE, visitor, CTX)
    // },

    [SyntaxKind.ImportKeyword]: (NODE, visitor, CTX) => {
        //     console.log("ğŸš€ ---> file: transpilers.js ---> line 31 ---> CTX",)
        if (NODE.parent.arguments) {
            NODE.parent.arguments[0] = CREATE_Plus_Token_Nodes([createStringLiteral(CTX.getCompilerOptions().__Url_Dir_Path), NODE.parent.arguments[0]])
        }
        return ts.createIdentifier("ASYNC_IMPORT_POLYFIL")
    },
    [SyntaxKind.RequireKeyword]: (NODE, visitor, CTX) => {
        return ts.createIdentifier("ssssssssssssss")
    },
    // [SyntaxKind.CallExpression]: (NODE, visitor, CTX) => {
    // console.log("ğŸš€ ---> file: transpilers.js ---> line 43 ---> NODE", NODE.expression.escapedText)
    //     return ts.createIdentifier("ssssssssssssss")
    // },

    // [SyntaxKind.ExportAssignment]: (NODE, visitor, CTX) => { 
    //     // return visitEachChild(NODE, visitor, CTX)
    // },

    [SyntaxKind.SourceFile]: (NODE, visitor, CTX) => {
        if (NODE.before_visited) return NODE

        const compilerOptions = CTX.getCompilerOptions()
        const ModuleColection = CTX.ModuleColection = configModules(NODE, compilerOptions)
        if (NODE.imports.length) {
            const { externalImports } = collectExternalModuleInfo(CTX, NODE, CTX.getEmitResolver(), compilerOptions);
            externalImports.length && NODE.statements.splice(0, 0, CREATE_Const_Variable(externalImports.map((importNode) => {
                const { Module_INDEX } = ModuleColection[importNode.moduleSpecifier.text]


                return [
                    getLocalNameForExternalImport(factory, importNode, NODE),
                    CREATE_Property_Access_Expression([compilerOptions.__Import_Module_Name, getColumnName(Module_INDEX)])
                ]
            })))
        }


        NODE.before_visited = true;
        return visitEachChild(NODE, visitor, CTX)
    }
}


export const transpilerAfter = {
    [SyntaxKind.SourceFile]: (NODE, visitor, CTX) => {
        if (NODE.After_visited) return NODE
        const moduleInfo = ModulesThree.get(NODE.originalFileName)

        if (moduleInfo.KindName === "ImportKeyword") {
            // áƒ¡áƒ˜áƒœáƒ¥áƒ¥áƒ áƒáƒœáƒ£áƒšáƒ˜ áƒ›áƒáƒ“áƒ£áƒšáƒ˜áƒ¡ áƒ˜áƒ›áƒáƒáƒ áƒ¢áƒ˜
            // NODE.statements = []
        }
        try {


            const newStatements = NODE.statements[0].expression.arguments.pop().body.statements

            NODE.statements = [CREATE_Export_File_Function(newStatements, moduleInfo.Module_INDEX, CTX.getCompilerOptions().__Import_Module_Name)]
        } catch (error) {

        }

        NODE.After_visited = true;
        return NODE
    }
}